import { Component, Input } from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { MultipleLnsConnectorService } from './multiple-lns-connector.service';
import { saveAs } from 'file-saver';
import { IApplication } from '@c8y/client';

@Component({
  selector: 'connection-info-with-download-csv',
  templateUrl: './connection-info-with-download-csv.component.html'
})
export class ConnectionInfoWithDownloadCsvComponent {
  @Input() messageData: any;
  @Input() appData: IApplication;
  @Input() modalTitle: string;
  constructor(private modal: BsModalRef, private connectorService: MultipleLnsConnectorService) {}

  dismiss() {
    this.modal.hide();
  }

  async download() {
    const url = `/service/${this.appData.contextPath}${this.messageData.URL}`;
    const res = await this.connectorService.download(url);
    const streamData = await res.blob();
    saveAs(streamData, 'DeviceList.csv');
  }
}
