export enum ConnectionType {
  ACTILITY,
  SIGFOX
}
export interface Connection {
  name: string;
  baseUrl: string;
  username: string;
  password: string;
  description: string;
}
export interface SigfoxConnection extends Connection {
  parentGroupId: string;
}
export interface ActilityConnection extends Connection {
  profileId: string;
  routeApplicationServerId?: string;
  routeApplicationServerKey?: string;
  adminApiVersion: string;
  coreApiVersion: string;
}
export function isSigfoxConnection(
  connection: SigfoxConnection | ActilityConnection
): connection is SigfoxConnection {
  return typeof (connection as SigfoxConnection).parentGroupId !== 'undefined';
}
export function isActilityConnection(
  connection: SigfoxConnection | ActilityConnection
): connection is ActilityConnection {
  return typeof (connection as ActilityConnection).profileId !== 'undefined';
}

export type State =
  | 'loadingConnection'
  | 'loadingError'
  | 'connectionAvailable'
  | 'connectionNotAvailable'
  | 'addConnections'
  | 'savedSuccessfully'
  | 'updateConnection';
