import { Component, HostListener, OnInit, ViewChild } from '@angular/core';
import { AlertService, gettext, ModalService, Status } from '@c8y/ngx-components';
import { MultipleLnsConnectorService } from './multiple-lns-connector.service';
import { head, orderBy, findIndex } from 'lodash-es';
import { TranslateService } from '@ngx-translate/core';
import { ConnectionType, SigfoxConnection, State } from './multiple-lns-connector.model';
import { ConnectionInfoWithDownloadCsvComponent } from './connection-info-with-download-csv.component';
import { BsModalService } from 'ngx-bootstrap/modal';
import { NgForm } from '@angular/forms';

@Component({
  selector: 'sigfox-multiple-lns-connector',
  templateUrl: './sigfox-multiple-lns-connector.component.html'
})
export class SigfoxMultipleLnsConnectorComponent implements OnInit {
  state: State = 'loadingConnection';
  connection = {} as SigfoxConnection;
  connections = Array<SigfoxConnection>();
  showPassword: boolean = false;
  @ViewChild('connectorsForm', { static: false }) connectorsForm: NgForm;
  connectionBeingEdited: string;
  cardHeader = gettext('Sigfox connections');

  constructor(
    private connectorService: MultipleLnsConnectorService,
    private alertService: AlertService,
    private translateService: TranslateService,
    private modalService: BsModalService,
    private modal: ModalService
  ) {}

  async ngOnInit() {
    await this.loadConnections();
  }

  async loadConnections() {
    const res = await this.connectorService.list(ConnectionType.SIGFOX);
    if (res && res.status !== 200) {
      const data = res.json ? await res.json() : undefined;
      this.alertService.addServerFailure({ data, res });
      this.state = 'loadingError';
    } else {
      const list = await res.json();
      this.connections = orderBy(list, ['name'], ['asc']);
      await this.setModel();
    }
  }

  async setModel(connectionObj = null) {
    await this.resetEditedUnsavedConnection();
    this.connection = connectionObj
      ? connectionObj
      : this.state === 'savedSuccessfully'
      ? this.connection
      : head(this.connections);
    this.state = 'updateConnection';
    this.showPassword = false;
    this.connectionBeingEdited = this.connection ? this.connection.name : undefined;
  }

  async resetEditedUnsavedConnection() {
    const originalConnectionIndex = (!this.connection) ? 0 : findIndex(this.connections, { name: this.connectionBeingEdited });
    if (this.connectionBeingEdited && this.state === 'updateConnection') {
      const isConnectionExist = await this.connectorService.detail(
        ConnectionType.SIGFOX,
        this.connectionBeingEdited
      );
      this.connections[originalConnectionIndex] = isConnectionExist as SigfoxConnection;
    }
  }

  async addConnection() {
    await this.resetEditedUnsavedConnection();
    this.connection = {} as SigfoxConnection;
    this.connectionBeingEdited = '';
    this.state = 'addConnections';
    this.showPassword = true;
  }

  async save() {
    const checkForConnectionName =
      (this.connectionBeingEdited && this.connectionBeingEdited !== '') ? this.connectionBeingEdited : this.connection.name;
    const isConnectionExist = await this.connectorService.exists(
      ConnectionType.SIGFOX,
      checkForConnectionName
    );
    if (this.state === 'addConnections' && isConnectionExist) {
      const mesg = this.translateService.instant(
        gettext(`Connection with name "{{ name }}" already exists.`),
        { name: this.connection.name }
      );
      this.alertService.danger(mesg);
    } else {
      this.saveConnection();
    }
  }

  async deleteConnection(connection) {
    const mesg = this.translateService.instant(
      gettext(`You are about to delete the connection "{{ name }}". Do you want to proceed?`),
      { name: connection.name }
    );

    try {
      await this.modal.confirm(gettext('Delete connection'), mesg, Status.DANGER, {
        ok: gettext('Delete'),
        cancel: gettext('Cancel')
      });

      await this.delete(connection);

    } catch (error) {
      // empty catch block
    }
  }

  changePassword() {
    this.showPassword = !this.showPassword;
    if (this.connectorsForm.controls.password) {
      this.connectorsForm.controls.password.setValue(null);
    }
  }

  private async saveConnection() {
    let res;
    res = await this.connectorService.save(this.connection, this.connectionBeingEdited);
    if (res && (res.status === 201 || res.status === 200)) {
      this.state = 'savedSuccessfully';
      this.alertService.success(gettext('Connection saved.'));
      await this.loadConnections();
    } else if (res && res.status === 500) {
      const data = res.json ? await res.json() : undefined;
      const app = this.connectorService.getApplication('sigfox-agent');
      const initialState = {
        messageData: data,
        appData: app,
        modalTitle: gettext('Failed to update the connection')
      };
      this.modalService.show(ConnectionInfoWithDownloadCsvComponent, { initialState });
    } else {
      const data = res.json ? await res.json() : undefined;
      this.alertService.addServerFailure({ data, res });
    }
  }

  private async delete(connection) {
    try {
      const response = await this.connectorService.delete(connection);

      if (response.ok && response.status === 204) {
        this.alertService.success(gettext('Connection deleted.'));
        await this.loadConnections();
      } else if (response && response.status === 500) {
        const data = response.json ? await response.json() : undefined;
        const app = this.connectorService.getApplication('sigfox-agent');
        const initialState = {
          messageData: data,
          appData: app,
          modalTitle: gettext('Failed to delete the connection')
        };
        this.modalService.show(ConnectionInfoWithDownloadCsvComponent, { initialState });
      } else {
        const data = response.json ? await response.json() : undefined;
        this.alertService.addServerFailure({ data, response });
      }
    } catch (error) {
      // empty catch block
    }
  }
}
