import { Component, Input, OnInit, ViewChild } from '@angular/core';
import { LpwanSetDeviceProtocolService } from './lpwan-set-device-protocol.service';
import { AlertService, gettext, PopoverConfirmButtons, PopoverConfirmComponent } from '@c8y/ngx-components';
import { IManagedObject, IResultList } from '@c8y/client';
import { pipe } from 'rxjs';
import { map } from 'rxjs/operators';
import { find } from 'lodash-es';
import { ActilityConnection, SigfoxConnection } from './multiple-lns-connectors/multiple-lns-connector.model';

@Component({
  selector: 'set-lns-connections',
  templateUrl: './lpwan-set-connections.component.html'
})
export class LpwanAssignLnsConnectionsComponent implements OnInit {
  loading: boolean;
  @Input() device: IManagedObject;
  buttons: PopoverConfirmButtons[] = [
    { label: gettext('Cancel'), action: () => Promise.reject() },
    { label: gettext('Re-register'), action: () => this.deviceReregister(), status: 'default' }
  ];
  reRegisterMsg = gettext('To change provider connection, you need to re-register device first.');
  filterConnection = pipe();
  pattern = '';
  @ViewChild(PopoverConfirmComponent, {static: true}) popoverConfirm: PopoverConfirmComponent;

  newConnection: ActilityConnection | SigfoxConnection;
  availableConnections: IResultList<ActilityConnection | SigfoxConnection>;
  currentConnection: ActilityConnection | SigfoxConnection;

  constructor(
    private lpwanService: LpwanSetDeviceProtocolService,
    private alertService: AlertService
  ) {}
  async ngOnInit() {
    await this.reload();
    this.setConnectionPipe('');
  }

  setConnectionPipe(filterStr: string) {
    this.pattern = filterStr;
    this.filterConnection = pipe(
      map((connections: IManagedObject[]) =>
        connections.filter(
          connection =>
            (!this.currentConnection || this.currentConnection.name !== connection.name) &&
            (!filterStr || connection.name.toLowerCase().indexOf(filterStr.toLowerCase()) > -1)
        )
      )
    );
  }

  async reload() {
    this.loading = true;
    this.newConnection = null;
    try {
        this.availableConnections = await this.lpwanService.getAvailableConnections(this.device);
        this.currentConnection = find(this.availableConnections.data, {
          name: this.device.c8y_LpwanDevice.lnsConnectionName
        });
    } catch (ex) {
      this.alertService.addServerFailure(ex);
    } finally {
      this.loading = false;
    }
  }

  togglePopover(popoverCfrm) {
    popoverCfrm.show();
    this.setConnectionPipe('');
  }

  deviceReregister() {
    return new Promise((resolve, reject) => {
      window.location.href = `#/deviceregistration`;
      resolve(true);
    });
  }
}
