import { Component, EventEmitter, Output, Input } from '@angular/core';
import { gettext } from '@c8y/ngx-components';
@Component({
  selector: 'no-connections-found',
  templateUrl: './no-connections-found.component.html'
})
export class NoConnectionsFoundComponent {
  @Output() onAction: EventEmitter<void> = new EventEmitter();
  @Input() header: string = gettext('Connections');

  addConnection() {
    this.onAction.emit();
  }
}
