import { Component, OnInit, ViewChild } from '@angular/core';
import { LpwanSetDeviceProtocolService } from './lpwan-set-device-protocol.service';
import { AlertService, gettext } from '@c8y/ngx-components';
import { IManagedObject, InventoryService, IResultList } from '@c8y/client';
import { Router } from '@angular/router';
import { pipe } from 'rxjs';
import { map } from 'rxjs/operators';
import { NgForm } from '@angular/forms';

@Component({
  selector: 'set-device-protocol',
  templateUrl: './lpwan-set-device-protocol.component.html'
})
export class LpwanAssignDeviceProtocolComponent implements OnInit {
  loading: boolean;
  device: IManagedObject;
  currentProtocol: IManagedObject;
  availableProtocols: IResultList<IManagedObject>;
  newProtocol: IManagedObject;
  filterProtocols = pipe();
  pattern = '';
  @ViewChild('lpwanSetDeviceProtocolForm', { static: false }) lpwanSetDeviceProtocolForm: NgForm;

  constructor(
    private lpwanService: LpwanSetDeviceProtocolService,
    private alertService: AlertService,
    private router: Router,
    private inventoryService: InventoryService
  ) {}
  async ngOnInit() {
    await this.reload();
    this.setPipe('');
  }

  setPipe(filterStr: string) {
    this.pattern = filterStr;
    this.filterProtocols = pipe(
      map((protocols: IManagedObject[]) =>
        protocols.filter(
          protocol =>
            (!this.currentProtocol || this.currentProtocol.id !== protocol.id) &&
            (!filterStr || protocol.name.toLowerCase().indexOf(filterStr.toLowerCase()) > -1)
        )
      )
    );
  }

  async reload() {
    this.loading = true;
    this.newProtocol = null;
    try {
      await this.loadDevice();
      this.availableProtocols = await this.lpwanService.getAvailableProtocols(this.device);
      this.currentProtocol = await this.lpwanService.getCurrentProtocol(this.device);
    } catch (ex) {
      this.alertService.addServerFailure(ex);
    } finally {
      this.loading = false;
    }
  }

  async loadDevice() {
    const deviceId = this.router.routerState.snapshot.url.match(/\d+/)[0];
    const { data } = await this.inventoryService.detail(deviceId);
    this.device = data;
  }

  async apply(selectedProtocol) {
    try {
      const moUpdated =
        (await this.lpwanService.applyProtocol(this.device, selectedProtocol))
          .res.status === 200;
      await this.reload();
      this.alertService.success(gettext('Device protocol set.'));
      this.lpwanSetDeviceProtocolForm.reset('dirty');
      if (moUpdated) {
        this.refreshCache();
      }
    } catch (ex) {
      this.alertService.danger(gettext('Could not set device protocol.'));
    }
  }

  async refreshCache() {
    try {
      await this.lpwanService.refreshCache(this.device);
    } catch (ex) {
      // do nothing (refreshing is an optional step)
    }
  }
}
